// toggle collapse on submenu
$("#navbarToggle").on("click", function () {
	var $this = $(this);

	// dropdown toggle setup
	if (primaryNavigation.hasClass("is-expanded")) {
		primaryNavigation.removeClass("is-expanded");
		$("body").removeClass("no-scroll");
		siteHeader.removeClass("nav-open");
		$this.addClass("collapsed");
		$this.attr("aria-expanded", false);

		// change the text to "Menu" when collapse
		$(".toggle-text").text("Menu");
	} else {
		primaryNavigation.addClass("is-expanded");
		$("body").addClass("no-scroll");
		siteHeader.addClass("nav-open");
		$this.removeClass("collapsed");
		$this.attr("aria-expanded", true);
		// change the text to "Close" when not collapsed
		$(".toggle-text").text("Close");
	}
});

$(".nav-item-back").each(function () {
	$(this).closest(".dm-menu").find(".dm-toggle").addClass("test").hide();
});

// prevent dropdown from closing when clicking on anything except an anchor
$(".dm-menu").on("click", function (e) {
	e.stopPropagation();
});

// close dropdown when clicking on mobile "back" button
$(".dm-back").on("click", function () {
	$(this).closest(".dm-menu").dropdown("toggle");
});

// dropdowns
$("body").on("click", ".dm-toggle", function (e) {
	e.preventDefault();
	var $currentDropdown = $(this).closest(".dropdown");
	var isExpanded = $currentDropdown.hasClass("show");

	// close all other dropdowns
	$(".dropdown").removeClass("show");
	$(".dm-menu").removeClass("show");
	$(".dm-toggle").attr("aria-expanded", "false");

	// toggle aria-expanded attribute for the clicked toggle
	$(this).attr("aria-expanded", !isExpanded);

	if (!isExpanded) {
		// open the clicked dropdown
		$currentDropdown.addClass("show");
		$currentDropdown.find(".dm-menu").addClass("show");
	}
});

$(document).on("click", function (e) {
	var $dropdown = $(".dropdown");

	if (!$dropdown.is(e.target) && $dropdown.has(e.target).length === 0) {
		$dropdown.removeClass("show");
		$(".dm-toggle").attr("aria-expanded", "false");
		$(".dm-menu").removeClass("show");
	}
});

function closeMenus(){
	$('#primaryNavigation .sub-menu').removeClass('show');

}
function resetTabIndex(){
	$('#primaryNavigation .sub-menu a').attr('tabindex', -1);
	$('#primaryNavigation .sub-menu button').attr('tabindex', -1);
}
resetTabIndex();

$(".menu-item-has-children > a").on("click", function (e) {
	e.preventDefault();
	resetTabIndex();
	let $menu = $(this).next(".sub-menu");
	if($menu.hasClass('show')){
		$menu.removeClass('show');
		return;
	} 
	closeMenus();
	window.returnFocusTo = $(this);
	$menu.addClass("show");
	$menu.find('button').attr('tabindex', 0);
	$menu.find('a').attr('tabindex', 0);
});

// close the current dropdown and remove the .show classes
$b.on("click", ".link-back", function () {
	$(".sub-menu").removeClass("show");
});

function populatePreview(id) {
	let $menuItem = $(`#${id}`);
	let $preview = $menuItem.closest(".body").next(".preview");
	$preview.html("");
	let data = window.menuData.find((obj) => obj.item === id);
	if (typeof data !== undefined && data.description) {
		$preview.closest(".sub-menu").removeClass("empty-preview");
		$preview.append(
			`<strong class="preview-heading">${data.title}</strong>`
		);
		$preview.append(`<p>${data.description}</p>`);
		$preview.append(
			`<a href="${$menuItem
				.children("a")
				.attr("href")}" class="btn btn-primary">${
				data.buttonText ? data.buttonText : "Learn more"
			}</a>`
		);
		return;
	}
	$preview.closest(".sub-menu").addClass("empty-preview");
}

$(".nav-primary > .menu-item-has-children").each(function () {
	let $thisLink = $(this).children("a");
	let $body = $(this).find(".body");

	if ($body.find(".menu-item").length === 1) {
		$(this).addClass("full-preview");
	}

	$body.prepend(
		`<button class="title">${$thisLink.text()}</button>`
	);
	$(this)
		.find(".sub-menu")
		.prepend(
			`<button class="link-back"><svg xmlns="http://www.w3.org/2000/svg" width="10" height="16" fill="none">
			<use href="#navigate-left" tabindex="-1"></use>
		</svg><span class="visually-hidden">back to ${$thisLink.text()}</span></button>`
		);
	populatePreview($(this).find(".menu-item").first().attr("id"));
});

$(".nav-primary .body .menu a").on("mouseenter focus", function () {
	if (window.innerWidth >= window.breakpoints.lg) {
		populatePreview($(this).closest('li').attr("id"));
	}
});

$b.on("click", ".social-share", function (e) {
	e.preventDefault();
	window.open(
		$(this).attr("href"),
		"shareWindow",
		"height=450, width=550, top=" +
			($(window).height() / 2 - 275) +
			", left=" +
			($(window).width() / 2 - 225) +
			", toolbar=0, location=0, menubar=0, directories=0, scrollbars=0"
	);
});

function closeSearch(){
	$b.removeClass('site-search-open');
	window.helpers.returnFocus();
}

$('#site-search-open').on('click', function(e){
	e.preventDefault();
	$b.addClass('site-search-open');
	window.returnFocusTo = $(this);
	window.helpers.trapFocus("#site-search");
});

$('#site-search-close').on('click', function(e){
	e.preventDefault();
	closeSearch();
});