
$("body").on("click", "#filters-search-open", function (e) {
	e.preventDefault();
	$b.addClass("filters-search-open");
	window.returnFocusTo = $(this);
	window.helpers.trapFocus("#filters-search");
});
$("body").on("click", "#filters-search-close", function (e) {
	e.preventDefault();
	$b.removeClass("filters-search-open");
});
$(document).on("keyup", function (e) {
	if (window.helpers.isEscape(e)) {
		$b.removeClass("filters-search-open");
		window.helpers.returnFocus();
	}
});



function updateArchive(url = false, $form = false){
	let data = null;
	let replaceSelector = null;

	if ($form) {
		data = $form.serializeArray();
		url = $form.attr('action');
	}

	if(!url){
		console.error('Error: no URL to fetch.');
		return;
	}

	if(data) {
		const replace = data.find(field => field.name === 'replace');
		if (replace) {
			replaceSelector = replace.value;
		}
	}

	let $archive = $('#archive-list');
	$archive.addClass('working');
	$.ajax(
		url,
		{
			method: 'GET',
			data: data,
			success: function(data, textStatus, jqXHR){
				let $dom = $(data);
				if(replaceSelector) {
					$(replaceSelector).html($dom.find(replaceSelector).html());
				} else {
					$archive.html($dom.find('#archive-list').html());
				}
			},
			complete: function(){
				$archive.removeClass('working');
				$("body").removeClass('filters-search-open');
			}
		}
	);
}

$("body").on("change", ".filters .option", function () {
	let checked = $(this).prop("checked");
	let $group = $(this).closest("ul");
	let $singles = $group.find(".option.single");
	let $all = $group.find(".option.all");
	if ($(this).val() === "all") {
		if (checked) {
			$singles.prop("checked", false);
		}
	} else {
		if (checked) {
			$all.prop("checked", false);
		}
	}

	if (!$group.find(".option:checked").length) {
		$all.prop("checked", true);
	}

	if( $('#update-filters').length ){
		$('#update-filters').closest('li').show();
		return;
	}

	updateArchive(false, $('#archive-filters'));
});

$("body").on('click', ".sorter", function(e){
	e.preventDefault();
	$('#order').val($(this).data('nextorder'));
	$('#orderby').val($(this).data('key'));
	updateArchive(false, $('#archive-filters'));
});

$("body").on('click', "#update-filters", function(e){
	e.preventDefault();
	updateArchive(false, $('#archive-filters'));
});

$("body").on("click", "#reset-filters", function (e) {
	e.preventDefault();
	updateArchive(window.location.href);
});

$('body').on('click', "#archive-list .paging a", function(e){
	e.preventDefault();
	updateArchive($(this).attr('href'));
});

$('body').on('submit', "#archive-list #filters-search form", function(e){
	e.preventDefault();
	updateArchive(false, $('#filters-search form'));
});