import Swiper from "swiper/bundle";
import "swiper/css/bundle";

// timeline
$("body").on("timelineDataReady", function () {
	const swipers = document.querySelectorAll(
		".wp-block-bespoke-timeline .swiper, .wp-block-bespoke-step-carousel .swiper"
	);

	const styles = window.getComputedStyle(
		document.getElementsByTagName("html")[0]
	);
	const navItemWidth = styles
		.getPropertyValue("--timeline-navigation-item-width")
		.replace("px", "");

	const updateControlLabels = function (swiper, index) {
		let $prevBtn = $(swiper.navigation.prevEl);
		let $nextBtn = $(swiper.navigation.nextEl);
		// update controls
		let $prevNavBtn = $(
			`.timeline-navigation button[data-index="${index - 1}"]`
		);
		let $nextNavBtn = $(
			`.timeline-navigation button[data-index="${index + 1}"]`
		);

		$prevBtn.find(".label").text($prevNavBtn.find(".label").text());
		$nextBtn.find(".label").text($nextNavBtn.find(".label").text());
	};

	const updateNavigationPosition = function (swiper, index) {
		$(".timeline-navigation").scrollLeft(index * navItemWidth);
		$(`.timeline-navigation button`).removeClass("active");
		$(`.timeline-navigation button[data-index="${index}"]`).addClass(
			"active"
		);
	};

	const updateTimelineContent = function (index) {
		$(`.timeline-content .item`).removeClass("active");
		$(`.timeline-content .item[data-index="${index}"]`).addClass("active");
	};

	swipers.forEach((el, index) => {
		el.classList.add(`slider-timeline-${index}`);

		const swiper = new Swiper(`.slider-timeline-${index}`, {
			slidesPerView: 1,
			speed: 600,
			centeredSlides: true,
			navigation: {
				prevEl: `.slider-timeline-${index} .slider-control.prev`,
				nextEl: `.slider-timeline-${index} .slider-control.next`,
			},
			on: {
				init: function () {
					updateControlLabels(this, 0);
				},
				slideChange: function () {
					updateControlLabels(this, this.activeIndex);
					updateNavigationPosition(this, this.activeIndex);
					updateTimelineContent(this.activeIndex);
				},
			},
			breakpoints: {
				576: {
					slidesPerView: 2,
					spaceBetween: 20,
				},
				768: {
					slidesPerView: 3,
					spaceBetween: 30,
				},
			},
		});

		$(".timeline-navigation button").on("click", function (e) {
			e.preventDefault();
			let index = $(this).data("index");
			swiper.slideTo(index);
			$(".timeline-navigation button").removeClass("active");
			$(this).addClass("active");

			updateControlLabels(swiper, index);
			updateTimelineContent(index);
		});
	});
});

// feature
const sliderFeature = document.querySelectorAll(".slider-feature");

sliderFeature.forEach((sliderElement, index) => {
	sliderElement.classList.add(`slider-feature-${index}`);

	const slider = new Swiper(`.slider-feature-${index}`, {
		slidesPerView: 1,
		effect: "fade",
		fadeEffect: {
			crossFade: true,
		},
		speed: 800,
		longSwipesRatio: 0.01,
		navigation: {
			prevEl: `.slider-feature-${index} .slider-prev`,
			nextEl: `.slider-feature-${index} .slider-next`,
		},
		pagination: {
			el: `.slider-feature-${index} .slider-pagination`,
			clickable: true,
		},
	});
});

// articles
const sliderArticles = document.querySelectorAll(".slider-articles");

sliderArticles.forEach((sliderElement, index) => {
	sliderElement.classList.add(`slider-articles-${index}`);

	const slider = new Swiper(`.slider-articles-${index}`, {
		slidesPerView: 1,
		spaceBetween: 30,
		longSwipesRatio: 0.01,
		speed: 1200,
		navigation: {
			prevEl: `.slider-articles-${index} .slider-prev`,
			nextEl: `.slider-articles-${index} .slider-next`,
		},
		breakpoints: {
			576: {
				slidesPerView: 2,
				spaceBetween: 20,
			},
			768: {
				slidesPerView: 3,
				spaceBetween: 30,
			},
		},
	});
});

// single element
const sliderSingle = document.querySelectorAll(".slider-single");

sliderSingle.forEach((sliderElement, index) => {
	sliderElement.classList.add(`slider-single-${index}`);

	const slider = new Swiper(`.slider-single-${index}`, {
		allowTouchMove: false,
		slidesPerView: 1,
		effect: "fade",
		fadeEffect: {
			crossFade: true,
		},
		speed: 800,
		longSwipesRatio: 0.01,
		navigation: {
			prevEl: `.slider-single-${index} .slider-prev`,
			nextEl: `.slider-single-${index} .slider-next`,
		},
		pagination: {
			el: `.slider-single-${index} .slider-paging-label`,
			clickable: false,
			type: "custom",
			renderCustom: function (swiper, current, total) {
				return current + " of " + total;
			},
		},
	});
});
