"use strict";

class AccordionItem {
	constructor(domNode, parent) {
		this.parent = parent;
		this.rootEl = domNode;
		this.toggleEl = this.rootEl.querySelector(".accordion-toggle");
		this.panelEl = document.getElementById(
			this.toggleEl.getAttribute("aria-controls")
		);

		// get initial state
		this.open = this.toggleEl.getAttribute("aria-expanded") === "true";

		// add event listeners
		this.toggleEl.addEventListener("click", this.onButtonClick.bind(this));
	}

	onButtonClick() {
		this.parent.closeAll(this);
		this.toggle(!this.open);
	}

	toggle(open) {
		// don't do anything if the open state doesn't change
		if (open === this.open) {
			return;
		}

		// update the internal state
		this.open = open;

		// handle DOM updates
		this.toggleEl.setAttribute("aria-expanded", `${open}`);
		if (open) {
			this.panelEl.removeAttribute("hidden");
			// get how tall this should be
			this.panelEl.style.height = "auto";
			let height = this.panelEl.offsetHeight + "px";
			// immediately hide it
			this.panelEl.style.height = "0px";
			// then add the height so css can animate it
			setTimeout(() => {
				this.panelEl.style.height = height;
			}, 0);
		} else {
			this.panelEl.addEventListener(
				"transitionend",
				() => {
					this.panelEl.setAttribute("hidden", "");
				},
				{ once: true }
			);
			this.panelEl.style.height = "0px";
		}
	}

	// Add public open and close methods for convenience
	open() {
		this.toggle(true);
	}

	close() {
		this.toggle(false);
	}
}

class Accordion {
	constructor(domNode) {
		this.rootEl = domNode;

		const items = this.rootEl.querySelectorAll(
			".wp-block-bespoke-accordion-item"
		);

		this.siblings = [];
		items.forEach((el) => {
			this.siblings.push(new AccordionItem(el, this));
		});
	}
	closeAll(selectedEl) {
		this.siblings.forEach((el) => {
			if (el !== selectedEl) {
				el.close();
			}
		});
	}
}

// init accordions
const accordions = document.querySelectorAll(".wp-block-bespoke-accordion");
accordions.forEach((el) => {
	new Accordion(el);
});
